html,body {
	background-color: white;
	height: 100%;
	width: 100%;
}
div.col-md-1 {
	padding-left:5px;
	padding-right:5px;
}

.case-box {
	border-width: 2px;
	border-radius: 5px;
	border-style:solid;
	position: absolute;
	overflow-wrap: break-word;

}

textarea {
	position: relative;
	height: 70%;
	width: 100%;
	border-radius: 5px;
	-webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */    /* Firefox, other Gecko */
	box-sizing: border-box;         /* Opera/IE 8+ */
}

.board {
	height: 80%;
	width: 100%;
	
}

.col-cases {
	background-color: gray;
	opacity: .3;
	margin-left: 1px;
	margin-right: 1px;
	height: 100%;
	width: 100%;
	border-width: 2px;
	border-radius: 5px;
	border-color: darkblue;
	border-style:solid;
	-webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */    /* Firefox, other Gecko */
	box-sizing: border-box;         /* Opera/IE 8+ */

}

.row-cases{
	height: 80%;
	width: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-direction: row;
	    flex-direction: row;
}

.row-queue{
	height: 20%;
	width: 100%;
	border-width: 2px;
	border-radius: 5px;
	border-color: black;
	border-style:solid;
	-webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */    /* Firefox, other Gecko */
	box-sizing: border-box;         /* Opera/IE 8+ */
	background-color: gray;
	opacity: .3;
}

.row-header{
	text-align: center;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	width: 100%;

}

.cell-header{
	width:10%;
}



